@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,700&display=swap');

body {
  background-color: #444;
  color: white;
  padding-left: 1em;
  font-family: Ubuntu;
}

.folder {
}

.clickable {
  user-select: none;
  font-size: inherit;
  cursor: pointer;
}


.entry * {
  color: white;
}
.entry {
  color: white !important;
  bottom: 1px solid white;
  border-radius: 10px;
  padding: 1em;
  margin-bottom: 0.5em;
  display: flex;
  background-color: #222;
  box-shadow: inset -1px -1px #ffffff, inset 1px 1px #808080, inset -2px -2px #dfdfdf, inset 2px 2px #0a0a0a;
  border-radius: 0;
  background-color: #666;
}

.entry .name {
  padding-right: 0.5em;
  margin-right: 0.5em;
  border-right: 1px solid white;
}

.entry div {
  display: inline;
  white-space: pre;
}

.func {
  margin-left: 5px;
  margin-right: 5px;
  background: #356;
  padding: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  line-height: 1.5;
  border-radius: 5px;
}


/* 98.css | https://unpkg.com/98.css */

ul.tree-view {
  /* background: #ffffff; */
  background: #888;
}

* {
  /* color: #222222; */
  color: white;
}

ul.tree-view ul {
  /* border-left: 1px dotted #808080; */
  border-left: 1px dotted white;
}

ul.tree-view ul > li::before {
  /* border-bottom: 1px dotted #808080; */
  border-bottom: 1px dotted white;
}


/* 98.css | https://unpkg.com/98.css */

ul.tree-view ul > li:last-child::after {
  /* background: #ffffff; */
  background: #888;
}
